@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@500&display=swap');


* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Ubuntu',
    sans-serif;
}




.main-calculator {
  height: 100vh;
  width: 100vw;
  padding: 1rem;
  overflow: hidden;
  border-radius: 20px;
  box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.1);
  background: linear-gradient(180deg, gray, black);
}

.upper {
  height: 27%;
}

.history,
.result {
  font-size: 20px;
  font-weight: 600;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  padding: 0.5rem;
  height: 2rem;
  width: 100%;
  margin: 1rem 1rem 0 0;
}

.history {
  margin: 1.25rem 0 1.5rem 0;
}

.result {
  font-size: 35px;
  font-weight: 800;
}

.theme-changer {
  display: flex;
  flex-direction: row;
  padding: 2rem 0 0 1rem;
  color: #505659;
  font-size: 14px;
  /* margin: 1rem 0 0 0; */
}



.theme-changer-input {
  cursor: pointer;
  background: transparent;
  background: linear-gradient(180deg, #505659, gray);
  height: 1.2rem;
  width: 2.6rem;
  margin-right: 0.5rem;
  border-radius: 99px;
  border: 1px solid #6A7677;
  position: relative;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.4);
}

.theme-changer-after {
  position: absolute;
  top: 0;
  height: 1.1rem;
  width: 1.1rem;
  background-color: white;
  border-radius: 50%;
  transition: all 0.5s ease-in;
}


@media only screen and (min-width: 768px) {
  .calculator {
    height: 100%;
    min-height: 100vh;
    width: 100%;
    display: grid;
    place-items: center;
  }

  .main-calculator {
    height: 80%;
    max-width: 480px;
    width: 40%;
  }

  .history {
    margin: 1rem 0;
    height: 1.75rem;
  }

  .theme-changer {
    padding-top: 2rem;
  }

}