.keys {
  width: 100%;
  height: 65%;
  margin-top: 1rem;
  padding: 1rem;
  display: grid;
  place-items: center;
  grid-template-columns: repeat(4, 1fr);
  grid-row-gap: 5px;
}

.button {
  height: 4rem;
  width: 4.5rem;
  border: none;
  outline: none;
  cursor: pointer;
  opacity: 0.85;
  border-radius: 999px;
  font-size: 1.1rem;
  box-shadow:
    -1px -0.5px 3px rgba(255, 255, 255, 0.5),
    -1.5px -1px 3px rgba(255, 255, 255, 0.5),
    -2px -1.5px 3px rgba(255, 255, 255, 0.5),
    -2.5px -2px 3px rgba(255, 255, 255, 0.5),

    1px 0.5px 3px rgba(0, 0, 0, 0.25),
    1.5px 1px 3px rgba(0, 0, 0, 0.25),
    2px 1.5px 3px rgba(0, 0, 0, 0.25),
    2.5px 2px 3px rgba(0, 0, 0, 0.25)
}

.button:hover {
  opacity: 1;
}

.clear {
  font-size: 1.25rem;
  background: linear-gradient(145deg, rgb(255, 145, 0), rgba(0, 0, 0, 0.25)) rgb(255, 145, 0);
  color: white;
}

.digit {
  background: linear-gradient(135deg,
      rgba(255, 255, 255, 0.15),
      rgba(255, 255, 255, 0.15))
    /* rgba(255, 255, 255, 0.30)); */
}


.special {
  background: linear-gradient(145deg, rgba(255, 255, 255, 0.30), rgba(0, 0, 0, 0.1))rgba(255, 208, 0, 0.75);
}

.special2 {
  background: linear-gradient(145deg, rgba(255, 255, 255, 0.30), rgba(0, 0, 0, 0.1)) rgba(161, 161, 161, 0.5);

}

.arithmetic {
  background: linear-gradient(145deg, rgba(255, 255, 255, 0.30), rgba(0, 0, 0, 0.1)) rgba(255, 0, 128, 0.75);
}


.equal {
  font-size: 1.35rem;
  background: linear-gradient(145deg, rgba(255, 255, 255, 0.30), rgba(0, 0, 0, 0.1)) #7100c7;
}

@media only screen and (min-width: 768px) {
  .keys {
    padding: 1rem 0.15rem 0.15rem 0.15rem;
  }

  .button {
    height: 2.75rem;
    width: 3.45rem;
  }
}