@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@500&display=swap);
.keys {
  width: 100%;
  height: 65%;
  margin-top: 1rem;
  padding: 1rem;
  display: grid;
  place-items: center;
  grid-template-columns: repeat(4, 1fr);
  grid-row-gap: 5px;
}

.button {
  height: 4rem;
  width: 4.5rem;
  border: none;
  outline: none;
  cursor: pointer;
  opacity: 0.85;
  border-radius: 999px;
  font-size: 1.1rem;
  box-shadow:
    -1px -0.5px 3px rgba(255, 255, 255, 0.5),
    -1.5px -1px 3px rgba(255, 255, 255, 0.5),
    -2px -1.5px 3px rgba(255, 255, 255, 0.5),
    -2.5px -2px 3px rgba(255, 255, 255, 0.5),

    1px 0.5px 3px rgba(0, 0, 0, 0.25),
    1.5px 1px 3px rgba(0, 0, 0, 0.25),
    2px 1.5px 3px rgba(0, 0, 0, 0.25),
    2.5px 2px 3px rgba(0, 0, 0, 0.25)
}

.button:hover {
  opacity: 1;
}

.clear {
  font-size: 1.25rem;
  background: linear-gradient(145deg, rgb(255, 145, 0), rgba(0, 0, 0, 0.25)) rgb(255, 145, 0);
  color: white;
}

.digit {
  background: linear-gradient(135deg,
      rgba(255, 255, 255, 0.15),
      rgba(255, 255, 255, 0.15))
    /* rgba(255, 255, 255, 0.30)); */
}


.special {
  background: linear-gradient(145deg, rgba(255, 255, 255, 0.30), rgba(0, 0, 0, 0.1))rgba(255, 208, 0, 0.75);
}

.special2 {
  background: linear-gradient(145deg, rgba(255, 255, 255, 0.30), rgba(0, 0, 0, 0.1)) rgba(161, 161, 161, 0.5);

}

.arithmetic {
  background: linear-gradient(145deg, rgba(255, 255, 255, 0.30), rgba(0, 0, 0, 0.1)) rgba(255, 0, 128, 0.75);
}


.equal {
  font-size: 1.35rem;
  background: linear-gradient(145deg, rgba(255, 255, 255, 0.30), rgba(0, 0, 0, 0.1)) #7100c7;
}

@media only screen and (min-width: 768px) {
  .keys {
    padding: 1rem 0.15rem 0.15rem 0.15rem;
  }

  .button {
    height: 2.75rem;
    width: 3.45rem;
  }
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Ubuntu',
    sans-serif;
}




.main-calculator {
  height: 100vh;
  width: 100vw;
  padding: 1rem;
  overflow: hidden;
  border-radius: 20px;
  box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.1);
  background: linear-gradient(180deg, gray, black);
}

.upper {
  height: 27%;
}

.history,
.result {
  font-size: 20px;
  font-weight: 600;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  padding: 0.5rem;
  height: 2rem;
  width: 100%;
  margin: 1rem 1rem 0 0;
}

.history {
  margin: 1.25rem 0 1.5rem 0;
}

.result {
  font-size: 35px;
  font-weight: 800;
}

.theme-changer {
  display: flex;
  flex-direction: row;
  padding: 2rem 0 0 1rem;
  color: #505659;
  font-size: 14px;
  /* margin: 1rem 0 0 0; */
}



.theme-changer-input {
  cursor: pointer;
  background: transparent;
  background: linear-gradient(180deg, #505659, gray);
  height: 1.2rem;
  width: 2.6rem;
  margin-right: 0.5rem;
  border-radius: 99px;
  border: 1px solid #6A7677;
  position: relative;
  box-shadow: 3px 3px 5px rgba(0, 0, 0, 0.4);
}

.theme-changer-after {
  position: absolute;
  top: 0;
  height: 1.1rem;
  width: 1.1rem;
  background-color: white;
  border-radius: 50%;
  transition: all 0.5s ease-in;
}


@media only screen and (min-width: 768px) {
  .calculator {
    height: 100%;
    min-height: 100vh;
    width: 100%;
    display: grid;
    place-items: center;
  }

  .main-calculator {
    height: 80%;
    max-width: 480px;
    width: 40%;
  }

  .history {
    margin: 1rem 0;
    height: 1.75rem;
  }

  .theme-changer {
    padding-top: 2rem;
  }

}
